.sons-statistic .ant-statistic-title{ 
  font-family: 'Open Sans', sans-serif;
  font-size: 21px;
  font-weight: 600;
  font-style: italic;
  text-transform: uppercase;
}
.sons-statistic .ant-statistic-content-value, .sons-statistic .ant-statistic-content-value-int{ 
  font-family: 'Open Sans', sans-serif;
  font-size: 70px;
  font-weight: 300;
  font-style: normal;
  text-transform: uppercase;
  text-align: right;
}
.sons-statistic .ant-statistic-content{
  line-height: 1;
}

.fleet-colors .ant-statistic-title{ color:#7B95B4; }
.fleet-colors .ant-statistic-content-value, .fleet-colors .ant-statistic-content-value-int{ color:#5a6f88; }

.policy-colors .ant-statistic-title{ color:#a49685; }
.policy-colors .ant-statistic-content-value, .policy-colors .ant-statistic-content-value-int{ color:#876c4c; }

.service-colors .ant-statistic-title{ color:#4f6142; }
.service-colors .ant-statistic-content-value, .service-colors .ant-statistic-content-value-int{ color:#6b914f; }
