.add-item { 
    width: 90%; 
    border-color: #666; 
    border-style: dashed; 
    margin: .5rem 0; 
    color:#aaa; 
    background-color: #333333;
    text-transform: uppercase;
  }

  .add-item:hover,
  .add-item:focus { 
    border-color: #888; 
    color:#fff; 
    background-color: #555;
  }