.sons-button {
    color: var(--tahiti-gold);
    border: 2px solid transparent;
    border-radius:17.5px;
    background: var(--unselected-round-bg);
    box-shadow: var(--box-shadow);  
    padding: 0 12px;
    line-height: 10px;
    text-transform: uppercase;
    width: max-content;
}

.sons-button:hover {
  color: var(--tahiti-gold);
  border: 2px solid transparent;  
  background: var(--hover-round-bg); 
  box-shadow: var(--box-shadow);  
}

.sons-button:focus {
  color: var(--tahiti-gold);
  border: 2px solid transparent;  
  background: var(--selected-round-bg); 
  box-shadow: none;   
}

