/** ant-card *******************************************************************************************************************/

.ant-card {
  margin-bottom: 10px;
  margin-right: 10px;
}

.ant-card-head {
  padding: 0 2px !important;
  font-weight: bold;
  border-bottom: var(--bottom-border-line);
}

.ant-card-body {
  padding: 12px 2px !important;
}

/** ant-tabs *************************************************************************************************************************/

.ant-tabs-tab-btn {
  opacity: 0.7;
  color: var(--text-active);
}

.ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
  opacity: 1;
  color: var(--text-active);
}

.ant-tabs-tab:hover {
  color: inherit;
}

.ant-tabs-ink-bar {
  background: var(--text-active);
}

.ant-tabs-top > .ant-tabs-nav::before {
  border-bottom: var(--bottom-border-line);
}

.ant-tabs-content-holder {
  text-transform: initial;
}

.ant-tabs-nav {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), #000000);
}

.ant-tabs-tabpane {
  padding-bottom: 1rem;
}

/** ant-form-item-label ***************************************************************************************************************/
.ant-form label {
  font-weight: bold;
}

.labels150 .ant-form-item-label{width:150px;}

.ant-form-item-label label {
  color: var(--text-normal);
}

/** ant-select ************************************************************************************************************************/
.ant-select-dropdown {
  box-shadow: var(--box-shadow);
  border-style: solid;
  border-width: 1px;
  border-image-source: linear-gradient(to bottom, #d2d2d2, #777777);
  border-image-slice: 1;
}

.ant-select-item-option-selected:not(.ant-select-item-option-disabled) {
  color: var(--text-selected);
  background-color: var(--bg-input);
}

.ant-select-selection-item {
  background-color: var(--bg-input);
}

.ant-select-selector {
  border-top: 0 !important;
  border-left: 0 !important;
  border-right: 0 !important;
  border-bottom: 2px solid var(--text-normal);
  opacity: var(--opacity-input-unselected);
}

.ant-select:not(.ant-select-customize-input) .ant-select-selector {
  background-color: var(--bg-input) !important;
}

.ant-select-selector:hover {
  opacity: 1;
  border-bottom: 2px solid var(--text-active) !important;
}

.ant-select-selector:focus {
  opacity: 1;
  border-bottom: 2px solid var(--text-active) !important;
}

.ant-select-multiple .ant-select-selection-item {
  border: 2px solid transparent;
  border-top-left-radius: 17.5px; 
  border-bottom-left-radius: 17.5px;
  background: var(--unselected-round-bg);
}

.ant-select-multiple .ant-select-selection-item:hover {
  background: var(--hover-round-bg);
  color: var(--tahiti-gold);
}  

/** ant-input *************************************************************************************************************************/

.ant-input:hover,
.ant-input:focus,
.ant-input-focused {
  opacity: 1;
  border-bottom: 2px solid var(--tahiti-gold) !important;
  background-color: var(--bg-input);
}

.ant-input[disabled] {
  opacity: var(--opacity-input-unselected);
}

/** ant-input-number ******************************************************************************************************************/

.ant-input-number-input {
  height: revert;
}

.ant-input-number:hover,
.ant-input-number:focus,
.ant-input-number-focused {
  opacity: 1;
  border: 2px solid var(--tahiti-gold) !important;
  background-color: var(--bg-input);
}

/** ant-modal **************************************************************************************************************************/

.ant-modal-content {
  position: relative;
  background-color: #282828;
  background-clip: padding-box;
  border-style: solid;
  border-width: 1px;
  border-image-source: linear-gradient(178deg, #d2d2d2 -14%, #777777 48%);
  border-image-slice: 1;
  border-radius: 2px;
  -webkit-box-shadow: var(--box-shadow);
  box-shadow: var(--box-shadow);
  pointer-events: auto;
}

/** .ant-pagination ********************************************************************************************************************/

.ant-pagination-item {
  border: none;
  color: #686868 !important;
}

.ant-pagination-item:focus,
.ant-pagination-item:hover {
  border-bottom: 1px solid var(--tahiti-gold);
}

.ant-pagination-item:focus a,
.ant-pagination-item:hover a {
  color: var(--tahiti-gold);
}

.ant-pagination-item-active {
  border-bottom: 1px solid var(--tahiti-gold);
}

.ant-pagination-item-active a {
  color: var(--tahiti-gold);
}

.ant-pagination-disabled .ant-pagination-item-link,
.ant-pagination-disabled:hover .ant-pagination-item-link,
.ant-pagination-disabled:focus .ant-pagination-item-link {
  border: none;
}

.ant-pagination-prev .ant-pagination-item-link,
.ant-pagination-next .ant-pagination-item-link {
  border: none;
}

.ant-pagination-prev:hover button,
.ant-pagination-next:hover button {
  border-bottom: 1px solid var(--tahiti-gold);
}

.ant-pagination-prev:focus .ant-pagination-item-link,
.ant-pagination-next:focus .ant-pagination-item-link,
.ant-pagination-prev:hover .ant-pagination-item-link,
.ant-pagination-next:hover .ant-pagination-item-link {
  color: var(--tahiti-gold);
  border-bottom: 1px solid var(--tahiti-gold);
}

/** ant-switch *************************************************************************************************************************/

.ant-switch-handle {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.sons-switch {
  opacity: var(--opacity-input-unselected);
  border: 2px solid transparent;
  background: var(--unselected-round-bg);
}

.sons-switch:hover {
  opacity: var(--opacity-input-unselected);
  background: var(--hover-round-bg);
}

.ant-switch-checked {
  opacity: 1;
}

/** ant-tag ******************************************************************************************************************************/

.ant-tag {
  margin-bottom: 2px;
  opacity: var(--opacity-input-unselected);
  border: 2px solid transparent;
  border-top-left-radius: 17.5px; 
  border-bottom-left-radius: 17.5px;
  background: var(--unselected-round-bg);
}

.ant-tag:hover {
  opacity: 1;
  color: var(--tahiti-gold);
  box-shadow: var(--box-shadow);
  background: var(--hover-round-bg);
}

/** anticon *******************************************************************************************************************************/
.anticon {
  color: var(--text-normal) !important;
}

.anticon:hover {
  color: var(--tahiti-gold) !important;
}

