/** ant-switch ******************************************************************************************************************************/
.ant-switch-handle::before {
  box-shadow: 0 0 3px 3px rgba(113, 182, 217, 0.24);
  background-image: linear-gradient(to bottom, #b3dff5, #71b6d9);
