:root {
  --pale-peach: #ffe4b6;
  --brown-grey-76: rgba(151, 151, 151, 0.76);
  --tahiti-gold: #f18407;
  --dark-tahiti: #d27326;
  --greenish-cyan: #2cffd0;
  --bright-cyan-0: rgba(87, 255, 234, 0);
  --bg-input: #101010;
  --line-normal: #8c8c8c;
  --text-active: #ffffff;
  --text-normal: #ababab;
  --text-selected: var(--tahiti-gold);
  --bottom-border-line: 1px solid #7d7d7d;
  --opacity-input-unselected: 0.5;
  --box-shadow: 10px 10px 10px 0 rgba(0, 0, 0, 0.4);
  --main-bg: radial-gradient(circle at top left, #292929, #060606),
    radial-gradient(circle at top left, #2c2c2c, #0e0e0e),
    radial-gradient(circle at top left, #2e2e2e, #151515),
    radial-gradient(circle at top left, #313131, #1d1d1d);

  --unselected-round-bg: linear-gradient(to bottom, #282828, #282828)
      padding-box,
    linear-gradient(173deg, #d2d2d2 -9%, #777777 30%) padding-box,
    linear-gradient(173deg, #d2d2d2 -9%, #777777 30%) border-box;
  --hover-round-bg: linear-gradient(to bottom, #101010, #101010) padding-box,
    linear-gradient(173deg, #d2d2d2 -9%, #777777 30%) padding-box,
    linear-gradient(173deg, var(--tahiti-gold) -9%, var(--tahiti-gold) 30%)
      border-box;
  --selected-round-bg: linear-gradient(to bottom, #101010, #101010) padding-box,
    linear-gradient(173deg, #d2d2d2 -9%, #777777 30%) padding-box,
    linear-gradient(173deg, #d2d2d2 -9%, #777777 30%) border-box;
  --rect-radial-bg: radial-gradient(
    circle at top left,
    rgba(80, 80, 80, 0.4),
    rgba(19, 19, 19, 0.8)
  );
  --rect-linear-bg: linear-gradient(
    to bottom,
    rgba(19, 19, 19, 0.8),
    rgba(80, 80, 80, 0.4)
  );
}
